
table  {
	border: solid  1px;
	margin: 0 auto;
 	border-spacing: 10px 10px;
}

.lowerPanel{
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	width: 50%;
	height: 100px;
	margin: auto;
}

.cell {
  height: 70px;
  width: 70px;
  background-color: skyblue;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.player1BoxStyle {
	background-color: #fc6565;
	border-radius: 50%;
	height: 100%;
	width: 100%;
}

.player2BoxStyle {
	background-color: #fcf665;
	border-radius: 50%;
	height: 100%;
	width: 100%;
}
.playerDetails{
	display: flex;
	width: 170px;
}


.player1Color{
	background-color: #fc6565;
	border-radius: 50%;
	height: 25px;
	width: 25px;
}

.player2Color{
	background-color: #fcf665;
	border-radius: 50%;
	height: 25px;
	width: 25px;
}

.winner {
	position: fixed;
	top: 35%;
	font-family: cursive;
	font-weight: 700;
	font-size: 40px;
}

button {
	background-color: #5fd564;
  border-radius: 15%;
  color: white;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 10px 5px;
  cursor: pointer;
}
